import { NgModule } from '@angular/core';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';

import { ForumRoutingModule } from './forum-routing.module';
import { ForumComponent } from './forum.component';
import { ForumHomeComponent } from './home/forum.home.component';

import { ForumSidebarComponent } from './sections/sidebar/forum.sidebar.component';
import { ForumCrumbsComponent } from './sections/crumbs/forum.crumbs.component';
import { editorComponent } from './post/editor/editor.component';

import { TopicComponent } from './topic/topic.component';
import { ForumsComponent } from './forums/forums.component';
import { postComponent } from './post/post.component';

import { ForumSkeletonComponent } from './forum_skeleton/forum.skeleton.component';
import { sharedModule } from '../../shared.module';

@NgModule({
    imports: [
        CommonModule,
        ReactiveFormsModule,
        ForumRoutingModule,
        sharedModule,
        FormsModule
    ],
    declarations: [
        ForumComponent,
		ForumHomeComponent,
		ForumSidebarComponent,
        ForumCrumbsComponent,
        TopicComponent,
        ForumsComponent,
        postComponent,
        editorComponent,
        ForumSkeletonComponent
    ]
})
export class ForumModule { }