import { Component, OnInit, Input, Output, Renderer2, ElementRef, ViewChild, EventEmitter  } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { forumService, AlertService, bbcodeService } from '../../../../_services';
import { first } from 'rxjs/operators';

@Component({
  selector: 'component-editor',
  templateUrl: './editor.component.html'
})
export class editorComponent implements OnInit {
  form: FormGroup;
  @Output() editor = new EventEmitter<string>();
  fontSize_number: string = '5';
  fontColor_code: string = '#e66465';
  tableBorder_color: string = '0';
  tableBackground_color: string = '0';
  tableHeader_color: string = '0';
  borderColor_code: string = '#e66465';
  boxBackground_color: string = '#e66465';
  boxBackground_img: string = '';
  listType: string = '0';
  borderWidth: string = '2';
  listShow = [];
  tableHeaders = [];
  tableLines = [];

  constructor(private renderer: Renderer2, private bbcodeService: bbcodeService, private route: ActivatedRoute, private router: Router, private formBuilder: FormBuilder) {
    this.renderer.listen('window', 'click',(e:Event) => {
      const textarea = document.querySelector('textarea');
      const dropDowns = document.querySelectorAll('.showList');
      for(let i = 0; i < dropDowns.length; i++) {
        if(dropDowns[i].classList.contains('show')) {
          if(e.target == textarea) {
            dropDowns[i].classList.remove('show')
          }
        }
      }
    });
  }

  ngOnInit(): void {
  }

  emiteBBcode(data) {
    this.editor.emit(data);
  }

  optionBBcode(option, e) {
    e.preventDefault();
    let optionShema = {
        bbcode: '',
        value: '',
        list: []
    }
    switch (option) {
        case 'setSize':
            optionShema = {
                bbcode: 'size',
                value: this.fontSize_number,
                list: []
            }
        break;
        case 'setColor':
            optionShema = {
                bbcode: 'color',
                value: this.fontColor_code,
                list: []
            }
        break
        case 'setHighlight':
            optionShema = {
                bbcode: 'highlight',
                value: this.fontColor_code,
                list: []
            }
        break
        case 'createList':
            optionShema = {
                bbcode: 'list',
                value: this.listType,
                list: this.listShow
            }
        break
        case 'setBorder':
            optionShema = {
                bbcode: 'border',
                value: this.borderColor_code,
                list: [this.borderWidth]
            }
        break
        case 'createTable':
            let tableOption = {
              border_color: this.tableBorder_color,
              background_color: this.tableBackground_color,
              header_color: this.tableHeader_color,
              header: this.tableHeaders,
              lines: this.tableLines
            };
            optionShema = {
                bbcode: 'table',
                value: 'none',
                list: [tableOption]
            }
        break
        case 'createBox':
            optionShema = {
                bbcode: 'box',
                value: this.boxBackground_color,
                list: [this.boxBackground_img]
            }
        break
    }
    this.emiteBBcode(this.bbcodeService.optionInsert(optionShema));

    // pas la peine de me juger
    setTimeout(() => { this.hideDrop() }, 50);
  }

  insert(bbcode) {
    this.hideDrop();
    switch (bbcode) {
      case 'b':
      case 'i':
      case 's':
      case 'u':
      case 'left':
      case 'right':
      case 'center':
      case 'justify':
      case 'quote':
      case 'spoiler':
      case 'h1':
        this.emiteBBcode(this.bbcodeService.simpleInsert(bbcode));
      break;
      case 'font':
        this.font();
      break;
      case 'size':
        this.showDrop('.fontSize');
      break;
      case 'color':
        this.showDrop('.fontColor');
      break;
      case 'highlight':
        this.showDrop('.fontHighlight');
      break;
      case 'list':
        this.showDrop('.list');
      break;
      case 'border':
        this.showDrop('.border');
      break;
      case 'table':
        this.showDrop('.table');
      break;
      case 'box':
        this.showDrop('.box');
      break;
    }
  }

  hideDrop() {
    const dropDowns = document.querySelectorAll('.showList');
    for(let i = 0; i < dropDowns.length; i++) {
      if(dropDowns[i].classList.contains('show')) {
        dropDowns[i].classList.remove('show');
      }
    }
  }

  showDrop(name) {
    const dropDown = document.querySelector(name);
    dropDown.classList.add('show');
  }

  validator(option) {
    if(option == 'size') {
      if(this.fontSize_number < '2')
        this.fontSize_number = '2';
      else if(this.fontSize_number > '20')
        this.fontSize_number = '20';
    }
  }

  font() {
    const fontList = [
      { font: 'arial' },
      { font: 'Montserrat' },
      { font: 'Poppins' },
      { font: 'sf display' },
      { font: 'Georgia' },
      { font: 'Impact' },
      { font: 'Times new roman' },
      { font: 'Verdana' },
    ];

    const dropDown = document.querySelector('.fontList');
    dropDown.classList.add('show');
    const list = document.querySelector('.fontList ul');
    for(let i = 0; i < fontList.length; i++) {
      const li = document.createElement('li');
      const name = document.createElement('b');
      const exemple = document.createElement('span');
      li.append(fontList[i].font, name);
      li.append('/me Sort l\'arme de sa poche', exemple);
      li.innerHTML = '<b>'+fontList[i].font+'</b> <span style="font-family: '+fontList[i].font+';">/me Sort l\'arme de sa poche</span>';
      list.append(li);
    }
  }

  listAddElem() {
    this.listShow.push({value: ''});
  }

  listRemoveElem(index) {
    this.listShow.splice(index, 1);
  }

  listEditElem(index, e) {
    this.listShow[index].value = e.target.value;
  }

  addLineTable() {
    this.tableLines.push({columns: [{value: 'Votre contenu ici'}]});
    console.log(this.tableLines);
  }

  addHeaderTable() {
    this.tableHeaders.push({value: 'Une entête ici'});
  }

  addColumnTable(i) {
    this.tableLines[i].columns.push({value: 'Votre contenu ici'});
  }

  setTableValue(i, iColumn, event) {
    const target = event.target || event.srcElement || event.currentTarget;
    this.tableLines[i].columns[iColumn].value = target.innerText;
  }
}
