import { Component, Input, ViewEncapsulation } from '@angular/core';


@Component({
  selector: 'app-skeleton',
  templateUrl: './forum.skeleton.component.html',
  styleUrls: ['./forum.skeleton.component.less'],
  encapsulation: ViewEncapsulation.None
})
export class ForumSkeletonComponent {
  @Input() model: any;
  constructor() { }

  createRange(number){
    return new Array(number);
  }

  getrandom() {
    const random = Math.floor(Math.random() * (250 - 50 + 1) + 50);
    return random + 'px';
  }

}